import React, { Suspense } from "react";
import { isTagEmbed } from "../../../../../actions/api";
import { IS_THEME } from "../../../../../utils";
const Author = React.lazy(() => import("../../../Elements/Author/Author"));
const Time = React.lazy(() => import("../../../Elements/Time/Time"));
const Network = React.lazy(() => import("../../../Elements/Network/Network"));

const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const authorNameStyle = { color: ThemeRule.authorColor, fontFamily: ThemeRule.css_font };
  const sepratorStyle = { backgroundColor: ThemeRule.authorColor };
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;
  const icnshwhid = (ThemeRule.socialAction && IS_THEME(68)) ? true : false

  return <div className="tb_gf_author_wrapper">
    <div className="tb_gf_author">
      {postAuthor ? <Suspense fallback={<></>}><Author author={author} network={network} authorClass={'tb_gf_author_profile'} /></Suspense> : ''}
      <div className="tb_gf_author_info">
        {postAuthor ? <div className="tb_gf_authorname" style={authorNameStyle}>{author.name}</div> : ''}
        <div className="tb_gf_post_info">
          {postAuthor ? <div className="tb_gf_username" style={authorNameStyle}>{username}</div> : ''}
          {authorSeprator ? <div className="tb_gf_seprator" style={sepratorStyle}> </div> : ''}
          {personalization.postTime ? <Suspense fallback={<></>}><Time postTime={postTime} timeClass={`tb_gf_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} authorNameStyle={authorNameStyle} /> </Suspense> : ''}
        </div>
      </div>
    </div>
      {icnshwhid ?
      <div className="tb_gf_social_">
        <Suspense fallback={<></>}><Network networkClass={'tb_gf_social__ico'} network={network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} /></Suspense>
      </div> : null}
  </div>
}
export default AuthorDetails